/* src/app/pages/home/home.css */
.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 2rem;
  font-family: "Poppins", sans-serif;
  gap: 5rem;
}
.content-card {
  display: flex;
  align-items: center;
  gap: 2rem;
  background:
    linear-gradient(
      135deg,
      #13072e,
      #3f2182);
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 3rem 4rem;
  border-radius: 24px;
  max-width: 1300px;
  min-height: 700px;
  width: 100%;
  color: white;
}
.text-content {
  flex: 1;
}
.text-content h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1rem;
}
.text-content p {
  font-size: 1.1rem;
  line-height: 1.6;
  max-width: 500px;
  margin-bottom: 2.5rem;
  color: #dcdcdc;
}
.cta-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background-color: white;
  color: #333;
  border: none;
  padding: 1rem 2rem;
  border-radius: 999px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: transform 0.2s ease;
}
.cta-button:hover {
  transform: scale(1.05);
}
.image-content {
  flex: 1;
  max-width: 500px;
}
.image-content img {
  width: 100%;
  height: auto;
  border-radius: 16px;
  clip-path: polygon(25% 0, 100% 0, 100% 100%, 0% 100%);
}
.events-section {
  max-width: 1200px;
  width: 100%;
  text-align: center;
  color: white;
}
.section-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 3rem;
}
.events-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
  text-align: left;
}
.event-card {
  background:
    linear-gradient(
      135deg,
      #3f2182,
      #13072e);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.event-card:hover {
  transform: translateY(-10px);
}
.event-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.event-info {
  padding: 1.5rem;
}
.event-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0 0 0.5rem 0;
}
.event-date {
  font-size: 0.9rem;
  color: #dcdcdc;
  margin-bottom: 1rem;
}
.event-description {
  font-size: 1rem;
  color: #dcdcdc;
  line-height: 1.6;
}
